import request from '@/utils/request'

//

//
export function orderIndex(data) {
	return request({
		url: '/mall/order/search',
		method: 'post',
		data
	})
}

//
export function reviewIndex(page, perPage, deliverRank) {
	return request({
		url: '/mall/comment/index',
		method: 'get',
		params: {
			"page": page, //
			"per-page": perPage, //
			"deliver_rank": deliverRank //
		}
	})
}

//
//
export function shipping() {
	return request({
		url: '/mall/shipping/index',
		method: 'get',
	})
}
//
export function addShipping(form) {
	return request({
		url: '/mall/shipping/addshipping',
		method: 'post',
		data: {
			"shipping_code": form.code,
			"shipping_name": form.name,
			"shipping_desc": form.desc,
			"shipping_insure": form.insure
		}
	})
}

//
export function getProvince() {
	return request({
		url: '/mall/region/index',
		method: 'get'
	})
}

//
export function getTemplate() {
	return request({
		url: '/mall/shippingarea/listall',
		method: 'get'
	})
}


//
export function addTemplate(form) {
	return request({
		url: '/mall/shippingarea/create',
		method: 'post',
		data: {
			"shipping_area_name": "",
			"regions": [1, 338, 636],
			"shipping_code": "SF",
			"first_weight": "1000",
			"money": "8",
			"second_weight": "500",
			"add_money": "5"
		}
	})
}

// 
export function getcustom(form) {
    return request({
        url: '/mall/shippingarea/create',
        method: 'post',
        data: {
            "shipping_area_name": "",
            "regions": [1, 338, 636],
            "shipping_code": "SF",
            "first_weight": "1000",
            "money": "8",
            "second_weight": "500",
            "add_money": "5"
        }
    })
}


